

































































import { Component, Prop, PropSync, Vue, Watch } from 'vue-property-decorator';
import { TableCodeGroupVO } from '@/common/types';
import { deleteDataApi, patchDataApi, postDataApi } from '@/utils/apis';
import _ from 'lodash';
import { confirmDelete } from '@/utils/alerts';

@Component({
  name: 'CodeGroupEditDialog',
})
export default class extends Vue {
  @PropSync('dialog', { required: true, type: Boolean }) syncedDialog!: boolean;
  @Prop({ required: true }) readonly editItem!: TableCodeGroupVO;
  @Prop({ required: true }) readonly mode!: string | null;

  loading: boolean = false;

  @Watch('dialog')
  watchDialog(val: boolean) {
    if (val) {
      this.$refs.observer && (this.$refs.observer as any).reset();
    }
  }

  async save() {
    const isValid = await (this.$refs.observer as any).validate();
    if (!isValid) {
      return;
    }
    this.mode === '수정' ? this.patch() : this.create();
  }

  async create() {
    this.loading = true;
    const response = await postDataApi<TableCodeGroupVO>(
      `admin/codeGroups/`,
      this.editItem,
    );
    this.loading = false;
    if (_.startsWith(response.code, `S`)) {
      this.syncedDialog = false;
      this.$emit('finished');
    }
  }

  async patch() {
    this.loading = true;
    const response = await patchDataApi<TableCodeGroupVO>(
      `admin/codeGroups/`,
      this.editItem,
      this.editItem.codeGroup!,
    );
    this.loading = false;
    if (_.startsWith(response.code, `S`)) {
      this.syncedDialog = false;
      this.$emit('finished');
    }
  }

  async delete() {
    const result = await confirmDelete();
    if (result.value) {
      this.loading = true;
      const response = await deleteDataApi<TableCodeGroupVO>(
        `admin/codeGroups/`,
        this.editItem.codeGroup!,
      );
      this.loading = false;
      if (_.startsWith(response.code, `S`)) {
        this.$emit('finished');
      }
    }
  }
}
