












































































































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import {
  DataTableHeader,
  SelectItem,
  TableCodeGroupVO,
  TableCodeVO,
} from '@/common/types';
import { getCodeListApi, getListApi } from '@/utils/apis';
import envs from '@/constants/envs';
import ButtonSet from '@/components/speeddial/ButtonSet.vue';
import CodeEditDialog from '@/views/admin/code/components/CodeEditDialog.vue';
import DataTableFilter from '@/components/datatable/DataTableFilter.vue';

@Component({
  name: 'CodeList',
  components: {
    DataTableFilter,
    CodeEditDialog,
    ButtonSet,
  },
})
export default class extends Vue {
  @Prop({ required: true }) readonly parentItem!: TableCodeGroupVO;

  readonly envs: typeof envs = envs;

  AUTHORITY: SelectItem[] | null = null;
  mode: string | null = null;

  loading: boolean = false;
  sortBy: string[] = ['displayOrder'];
  sortDesc: boolean[] = [false];
  items: TableCodeVO[] = [];
  filteredItems: TableCodeVO[] = [];
  editItem: TableCodeVO = {};
  selected: TableCodeVO[] = [];
  dialog: boolean = false;
  headers: DataTableHeader[] = [
    {
      text: `상세 코드`,
      align: `start`,
      value: `code`,
    },
    {
      text: `상세 코드명`,
      align: `start`,
      value: `name`,
    },
    {
      text: `사용 가능`,
      align: `center`,
      value: `available`,
      filterType: 'switch',
      width: 100,
    },
    {
      text: `출력 순서`,
      align: `end`,
      value: `displayOrder`,
      width: 100,
    },
    {
      text: `권한(~부터 볼수 있음)`,
      align: `start`,
      value: `authority`,
      filterType: 'select',
      filterSelectItem: [],
    },
    {
      text: `작업 일시`,
      align: `center`,
      value: `updated`,
      filterable: false,
      width: 160,
    },
    {
      text: `작업자`,
      align: `start`,
      value: `updatedBy`,
      filterable: false,
      width: 100,
    },
  ];

  async mounted() {
    this.headers[4].filterSelectItem = this.AUTHORITY = await getCodeListApi(
      'AUTHORITY',
    );
  }

  @Watch('parentItem')
  watchParentItem(val: TableCodeGroupVO): void {
    this.items = [];
    if (val && val.codeGroup) {
      this.getList();
    }
  }

  async getList() {
    this.selected = [];
    this.items = [];
    this.loading = true;
    const response = await getListApi<TableCodeVO[]>(
      `admin/codes/${this.parentItem.codeGroup}`,
    );
    this.loading = false;
    this.items = response.data || [];
  }
}
